.dropDownV2 {
  background: #001275;
  height: 1.3rem;
  display: flex;
  align-items: center;
  padding: 0rem 0rem;
  /* overflow-y: hidden; */
}
.dropDownV2--title {
  /* background-color: blueviolet; */
  width: 100%;
  color: rgb(255, 255, 255);
  margin-right: 0.7rem;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.dropDownV2--icon {
  /* background-color: rgb(144, 238, 22); */
  width: auto;
  margin-left: auto;
  margin-top: -0.3rem;
  cursor: pointer;
}
