.side_bar{
    height: 100%;    
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;    
    overflow: hidden;
    transition: 0.5s;
    display: flex;
    flex-direction: row;
}
.side_bar__button{
    position: absolute;
    right: 0px;
    font-size: 36px;
    height: 100%;
    width: auto;
    background-color: rgb(76, 119, 76);
}
.side_bar__content{
    padding: 0px 20px 5px 5px; 
}