.search {
  /* position: relative; */
  /* width: 20%; */
  background: #020c17;
  height: 2rem;
  min-width: 80px;
  /* box-shadow: 0px 2px 4px rgb(150, 150, 150); */
  border-radius: 5px;
  position: relative;
  display: flex;
}

.search input {
  width: 100%;
  background: #020c17;
  border: none;
  border-right: none;
  padding: 0px 10px 0px 13px;
  height: 100%;

  border-radius: 5px 0px 0px 5px;
  outline: none;
  color: #acacac;
  font-size: 0.9rem;
}

.search__searchButton {
  width: 2.5rem;
  padding: 0.6rem;
  height: 100%;
  border: 0px solid #00b4cc;
  /* border-left: 2.5px solid #035a7073; */
  background: transparent;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-top: 0.2rem; */
}
